// src/pages/index.js

import { Link, graphql } from "gatsby"
import { styled } from "styled-components"
import React from "react"

import { Layout } from "../components/layout/layout"


const CategoryRepItem = props => (
  <div className="category-item" key={props.key}>
    <div className="category-item-title">
      <h3> <Link to={props.to}> {props.category}</Link></h3>
    </div>
  </div>
)


export default function IndexPage({ data }) {
  //export default ({ data }) => {
  const title = data.site.siteMetadata.title;

  return (
    <Layout root={title} title={title}>
      {data.allMarkdownRemark.edges.map(post => (
  
        <CategoryRepItem
          key={post.node.id}
          category = {post.node.frontmatter.title}
          to = {post.node.fields.slug}
        />

      ))}
    </Layout>
  )
}

// show category page links
export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
        url
      }
      pathPrefix
    }
    allMarkdownRemark(
      sort: { fields: frontmatter___date, order: DESC }
      filter: { frontmatter: { type: { eq: "category" } } }
    ) {
      edges {
        node {
          id
          frontmatter {
            category
            date(formatString: "YYYY-MM-DD")
            tags
            title
          }
          excerpt(pruneLength: 100)
          fields {
            slug
          }
        }
      }
    }
  }
`